@use '../abstracts/functions' as f;

.widgetHeader {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  background-color: f.color('background-widget-header');
  border-radius: 25px 25px 0 0;
  padding: 0 10px;

  min-height: 100px;
  font-size: 1.5em;

  .logoContainer {
    width: 80px;
    height: 100%;
    margin-right: 10px;
    background-image: url("data:image/svg+xml,%3Csvg width='56' height='56' viewBox='0 0 56 56' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Crect x='0.5' y='0.5' width='55' height='55' rx='27.5' stroke='white'/%3E%3Crect x='9.33398' y='9.33325' width='37.3333' height='37.3333' fill='url(%23pattern0)'/%3E%3Cdefs%3E%3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E%3Cuse xlink:href='%23image0_933_10448' transform='translate(-0.00436047) scale(0.0104167)'/%3E%3C/pattern%3E%3Cimage id='image0_933_10448' width='300' height='96' xlink:href='data:image/png;base64,'/%3E%3C/defs%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  .titleContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
    text-align: left;

    p {
      font-size: 22px;
      font-weight: 700;
    }
    span {
      font-size: 14px;
      font-weight: 300;
      color: rgba($color: f.colorRGB('general-white-pure'), $alpha: 0.8);
    }
  }

  .button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.5rem;
    cursor: pointer;
    height: 30px;
    width: 30px;
  }
}
